<template>
  <div class="recommend" ref="recommend">
    <scroll class="recommend-content" :data="disclists" ref="scroll">
      <div>
        <div v-if="recommends.length" class="slider-wrapper">
          <slider>
            <div v-for="item in recommends">
              <a :href="item.linkUrl"><img @load="loadImage"
                :src="https(item.picUrl)" class="needsclick"></a>
            </div>
          </slider>
        </div>
        <div class="recommend-list">
          <h1 class="list-title">热门歌单推荐</h1>
          <ul style="margin-right: -8px;overflow: hidden;">
            <li @click="selectItem(item)" v-for="item in disclists" class="item">
              <div class="icon">
                <img v-lazy="https(item.picUrl)">
              </div>
              <div class="text">
                <h2 class="name" v-html="item.songListDesc"></h2>
                <p class="desc" v-html="item.songListAuthor"></p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-container" v-show="!disclists.length">
        <loading></loading>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import {getRecommend} from 'api/recommend'
  import {ERR_OK} from 'api/config'
  import slider from 'base/slider/slider'
  import scroll from 'base/scroll/scroll'
  import loading from 'base/loading/loading'
  import {playlistMixin} from 'common/js/mixin'
  import {mapMutations} from 'vuex'

  export default {
    mixins: [playlistMixin],
    data () {
      return {
        recommends: [],
        disclists: []
      }
    },
    created () {
      this._getRecommend()
    },
    methods: {
      handlePlaylist (playlist) {
        const bottom = playlist.length ? '60px' : ''
        this.$refs.recommend.style.bottom = bottom
        this.$refs.scroll.refresh()
      },
      selectItem (item) {
        this.$router.push({
          path: `/recommend/${item.id}`
        })
        this.setDisc(item)
      },
      _getRecommend () {
        getRecommend().then((res) => {
          if (res.code === ERR_OK) {
            this.recommends = res.data.slider
            this.disclists = res.data.songList
          }
        })
      },
      loadImage () {
        if (!this.checkImg) {         /* 加载完一张图片时不再执行 */
          this.checkImg = true
          this.$refs.scroll.refresh()
        }
      },
      https (img) {
        return `https${img.substr(4)}`
      },
      ...mapMutations({
        setDisc: 'SET_DISC'
      })
    },
    components: {
      slider,
      scroll,
      loading
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .recommend
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .slider-wrapper
        position: relative
        width: 100%
        overflow: hidden
      .recommend-list
        margin:0 10px
        .list-title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          float: left
          width: 50%
          padding-right: 8px
          margin-bottom: 10px
          overflow: hidden
          box-sizing: border-box
          .icon
            position:relative
            margin-bottom:5px
            &:before
              content: ""
              display: block
              padding-top: 100%
            img
              position: absolute
              top: 0
              left: 0
              display: block
              width: 100%
              border-radius: 10px
          .text
            padding: 0 7px 5px
            font-size: $font-size-medium
            .name
              color: $color-text
              margin:5px 0
              no-wrap()
            .desc
              color: $color-text-d
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



